.gradient
  padding: 65px 0
  width: 100%
  height: 100vh
  display: flex
  justify-content: center
  align-items: center

  &__title
    font-family: 'Pacifico', cursive
    font-style: normal
    font-weight: 400
    font-size: 7em
    line-height: 2em
    background: linear-gradient(to right, #262C46, #482B47, #77395E, #994C7A, #B85279,#A5496E, #BF744A,#B77A41, #D9A460, #ECCC77, #DCD757,#B8C843, #B4C753)
    -webkit-text-fill-color: transparent
    -webkit-background-clip: text
