.animate
  position: relative
  top: 0
  left: 0
  width: 100%
  height: 100vh
  overflow: hidden
  background: #000
  &__title
    position: absolute
    top: 40%
    left: 50%
    transform: translate(-50%, -50%)
    font-family: 'Bebas Neue', cursive
    font-size: 6em
    line-height: 2em
    letter-spacing: 0.09em
    color: #fff//05FF00//00FF57
    padding: 0 20px
    text-shadow: 0 0 20px #05FF00
    &::after
      content: attr(data-text)
      position: absolute
      top: 0
      left: 0
      padding: 0 20px
      z-index: -1
      color: #05FF00
      filter: blur(15px)
    &::before
      content: ''
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      background: #00FF57
      z-index: -2
      opacity: 0.5
      filter: blur(40px)

.pattern
  transform-style: preserve-3d
  perspective: 1000px
  animation: animateBg 8s ease-in-out infinite
  &__face
    position: relative
    width: 100%
    height: 56vh
    background: url(../img/pattern2.webp)
    background-size: 100px 100px
    animation: animate 1s linear infinite
    &_2
      height: 100vh
      transform-origin: top
      transform: rotateX(70deg)
      &::before
        content: ''
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 125px
        background: linear-gradient(180deg, rgba(0,0,0,0.5),transparent)
    &_1::before
      content: ''
      position: absolute
      bottom: 0
      left: 0
      width: 100%
      height: 125px
      background: linear-gradient(0deg, rgba(0,0,0,0.5),transparent)
